body {
  --primary-color: #036D77;
  --secoondary-color: #E19478;
  --c-color-1: #001B20;
  --c-color-2: #FDDCD1;
  --header-font: "Source Serif Pro";
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
  font-weight: 400;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  line-height: 1em;
  margin: 0px;
}

.header {
  font-family: var(--header-font);
  color: var(--c-color-2);
  display: flex;
  flex-direction: column;
}
.header a {
  color: var(--c-color-2);
}
.header-sub {
  background-color: var(--primary-color);
  min-height: 45px;
  justify-content: center;
  align-items: center;
  display: none;
  margin: 0px;
}
.header-sub h2 {
  font-size: 22px;
  font-style: italic;
  padding-right: 40px;
  font-weight: 400;
}
.header-sub p {
  font-size: 20px;
}
.header-content {
  min-height: 183.37px;
  background-color: var(--c-color-1);
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header-content .menu {
  display: none;
}
.header-content .mobile-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.header-content .mobile-menu .menu-logo img {
  width: 100%;
  max-width: 183.24px;
}
.header-content .mobile-menu .toggle-menu {
  position: absolute;
  cursor: pointer;
  padding: 5px;
  top: 10px;
  right: 20px;
}
.header-content .mobile-menu .toggle-menu i {
  font-size: 29px;
}

@media (min-width: 950px) {
  .header {
    font-family: var(--header-font);
    color: var(--c-color-2);
  }
  .header a {
    color: var(--c-color-2);
  }
  .header-sub {
    background-color: var(--primary-color);
    min-height: 45px;
    justify-content: center;
    align-items: center;
    display: flex;
  }
  .header-content {
    min-height: 140px;
    font-size: 20px;
    padding: 27px 2.5%;
    display: block;
  }
  .header-content .menu {
    display: grid;
    grid-template-columns: 1fr 15vw 1fr;
    justify-self: center;
    align-self: start;
  }
  .header-content .menu-logo a img {
    width: 100%;
    height: auto;
    max-width: 200px;
  }
  .header-content .menu ul {
    margin: 0px;
    padding: 0px;
  }
  .header-content .menu ul:not(.sub-menu) {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }
  .header-content .menu ul .sub-menu {
    margin: 0px;
  }
  .header-content .menu ul .sub-menu li {
    padding: 0px !important;
  }
  .header-content .menu ul .sub-menu li a {
    font-size: 16px;
    line-height: 2em;
  }
  .header-content .menu ul li {
    padding: 0px 20px;
  }
  .header-content .menu ul li a {
    display: flex;
    text-align: center;
  }
  .header-content .mobile-menu {
    display: none;
  }
}